import './colors';
import './import-export';
import './interface';
import './options';
import './types';
import loading from '../utils/loading';
import { scrollToY } from '../utils/scroll-to-y';
window.addEventListener('load', function () {
    document.body.classList.remove('preload');
});
document.addEventListener('contextmenu', function () { return false; });
document.querySelectorAll('a').forEach(function (e) { return e.addEventListener('click', loading); });
document.querySelectorAll('.start').forEach(function (e) {
    e.addEventListener('contextmenu', function (e) {
        e.preventDefault();
        document.querySelector('.hiddenOptions').style.display = 'flex';
        scrollToY(document.body.scrollHeight, 400);
        return false;
    });
});
